.services {
    top:0;
    left: 0;
    padding-bottom: 50px;
    &__container {
        display: flex;
        justify-content: space-between;
    }
    &-left {
        width: 75%;
        display: flex;
        flex-wrap: wrap;
        &__item-container {
            width: 30%;
        }
        &__title {
            color: var(--color-black);
            font-size: 24px;
            font: var(--high-bold);
            margin: 0;
            padding: 20px 0;
        }
        &__link {
            color: var(--color-black);
            line-height: 1.8;
        }
    }
    &-right {
        width: 25%;
        display: flex;
        flex-direction: column;
        &__title {
            color: gray;
            font: var(--high-bold);
            font-size: 14px;
        }
        &__tel, &__address {
            color: var(--color-black);
            font: var(--high-bold);
            font-size: 18px;
            padding-bottom: 10px;
        }
        &__btn {
            font-weight: 400;
            font-size: 16px;
            background-color: var(--color-accent-1);
            padding: 10px;
            color: var(--color-white);
            cursor: pointer;
            width: 170px;
            height: 40px;
            &:hover {
                transition: 0.3s ease-in-out;
                opacity: 0.7;
            }
        }
        &__btn-link {
            text-decoration: none;
        }
    }
}