.footer {
    left: 0;
    top: 0;
    color: var(--color-black);
    font: var(--medium-regular);
    font-size: 16px;
    background-color: var(--color-white);
    &__container {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    
    &__list {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }
    &__item {
        display: flex;
        align-items: center;
        padding: 0 28px;
        height: 90px;
    }
    &__border {
        border-top: 1px solid #c4c4c4;
        
    }
    &__border:not(:last-child) {
        border-right: 1px solid #C4C4C4;
    }
    &__descr {
        display: flex;
        height: 90px;
        align-items: center;
    }
    &__text {
        color: var(--color-black);
        font-size: 12px;
        max-width: 50%;
        margin-left: 20px;
    }
    &__link {
        text-decoration: underline;
        color: var(--color-black);
        font-size: 12px;
        margin-left: 40px;
    }
}